<template>
  <div class="loading-screen">
    <div class="spinner">
      <svg
        width="120"
        height="120"
        viewBox="0 0 120 120"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <g clip-path="url(#clip0_364_1657)">
          <path
            d="M29.9977 37.5H4.36172C13.2633 15.5109 34.8211 0 60 0C93.1359 0 120 26.8641 120 60C120 93.1359 93.1359 120 60 120C34.8211 120 13.2633 104.489 4.36172 82.5C1.54922 75.5531 0 67.957 0 60H60V82.5H29.9977C36.8391 91.6078 47.7305 97.5 60 97.5C80.7117 97.5 97.5 80.7117 97.5 60C97.5 39.2883 80.7117 22.5 60 22.5C47.7305 22.5 36.8391 28.3922 29.9977 37.5Z"
            class="animated-path"
          />
        </g>
        <defs>
          <clipPath id="clip0_364_1657">
            <rect width="120" height="120" fill="white" />
          </clipPath>
        </defs>
      </svg>
    </div>
    <div class="loading-text">{{ animatedText }}</div>
  </div>
</template>

<script>
export default {
  name: "LoadingScreen",
  data() {
    return {
      baseText: "GLYPH is launching",
      animatedText: "GLYPH is launching",
    };
  },
  mounted() {
    this.animateColors();
    this.animateText();
  },
  methods: {
    animateColors() {
      const path = document.querySelector(".animated-path");
      path.style.fill = "#ffffff"; // Устанавливаем начальный цвет
      const changeColor = () => {
        const randomColor = `#${Math.floor(Math.random() * 16777215).toString(
          16
        )}`;
        path.style.transition = "fill 1s ease";
        path.style.fill = randomColor;
      };
      setTimeout(() => {
        setInterval(changeColor, 1000);
      }, 0); // Уменьшаем задержку перед началом анимации
    },
    animateText() {
      let dotCount = 0;
      setInterval(() => {
        dotCount = (dotCount + 1) % 4;
        this.animatedText = `${this.baseText}${".".repeat(dotCount)}`;
      }, 500);
    },
  },
};
</script>

<style scoped>
.loading-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #000000;
  color: #ffffff;
}

.loading-text {
  margin-top: 20px;
  font-size: 16px;
}

.animated-path {
  fill: #d71921; /* Устанавливаем начальный цвет, чтобы логотип был видим сразу */
}
</style>
